/* eslint-disable */
import React, {useEffect, useState} from 'react';
import {useHistory, useParams} from "react-router-dom";
import styled from 'styled-components';
import './Detail.scss';

/*let 박스 = styled.div`
  padding : 20px;
`;
let 제목 = styled.h4`
  font-size : 25px;
  color : ${ props => props.색상 };
`;*/

function Detail(props) {
    let history = useHistory();
    let { id } = useParams();
    let findGoods = props.shoes.find(function (goods) {
        return goods.id == id;
    });

    let [alert, alertChange] = useState(true);

    /* component 생성 후 timeOut 함수 실행 */
    /* 2초 지나면 alert를 false로 변환 */
    useEffect( () => {
        let timer = setTimeout( () => { alertChange(false), console.log("변경됨") }, 2000 );
        return () => { clearTimeout( timer ) }
    }, [ alert ] );

    return (
        <div className="container">
            <div className="row">
                <div className="col-md-6">
                    <img src= { "https://codingapple1.github.io/shop/shoes" + (findGoods.id + 1) + ".jpg" } width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5"> { findGoods.title } </h4>
                    {
                        alert === true
                            ? (<div className="my-alert2">
                                <p>재고가 얼마 남지 않았습니다</p>
                               </div>)
                            : null
                    }
                    <p> { findGoods.content } </p>
                    <p> { findGoods.price } 원 </p>
                    <button className="btn btn-danger" style={ { margin: "10px" } }
                    onClick = { () => { props.재고변경([9, 10, 11])}}>주문하기</button>
                    <button onClick = { () => {history.goBack()} } className = "btn btn-danger" style={ { margin: "10px" } }> 뒤로가기 </button>
                </div>
            </div>
            {/*<div>
                <박스>
                    <제목 색상="blue">안녕하세요1</제목>
                    <제목 색상={'red'}>안녕하세요2</제목>
                </박스>
            </div>*/}
            <div>
                <Info 재고 = { props.재고 }></Info>
            </div>
        </div>
    )
}

function Info(props) {
    return (
        <p> 재고 : { props.재고[0] } </p>
    )
}

export default Detail